// App internal variables
@main-header-height: 82px;
@main-nav-width: 86px;
@main-nav-width-collapse: 225px;

body.ss-dashboard-wrapper {
	background-color: #f7f9ff;
	div.dash-header,
	div.dash-nav,
	div.dash-footer {
		display: none !important;
	}

	&.newui .dashboard .pagecontent {
		background-color: #f7f9ff !important;
		height: auto;
		min-height: inherit;
		overflow-x: hidden;
	}

	.v-application {
		min-height: inherit;
	}

	.section {
		min-width: auto;
	}

	> div:not([id="SS-DashboardWrapper"]) {
		position: relative;
		z-index: 1;
	}

	&.newui {
		.UIPromtBox,
		.UIPromtBox.master {
			z-index: 99 !important;
		}
		.UIPromtBoxBG {
			top: @main-header-height;
			left: @main-nav-width;
			z-index: 99 !important;
		}

		&.menu-show-full .UIPromtBoxBG {
			left: @main-nav-width-collapse;
		}
	}

	@media screen and (min-width: 1601px) {
		div.pagecontent {
			padding: @main-header-height 0 0 @main-nav-width-collapse !important;
		}
	}

	@media screen and (min-width: 992px) and (max-width: 1600px) {
		div.pagecontent {
			padding: @main-header-height 0 0 @main-nav-width !important;
			background-color: #f7f9ff;
		}

		&.menu-show-full div.pagecontent {
			padding-left: @main-nav-width-collapse !important;
		}
	}

	@media screen and (min-width: 768px) {
		div.pagecontent {
			padding-top: @main-header-height !important;
		}
	}

	@media screen and (min-width: 992px) {
		.contentwrapper {
			margin: 0 !important;
			padding: 20px !important;
		}
	}

	.contentwrapper {
		margin: 12px !important;

		@media screen and (max-width: 767px) {
			padding-top: 56px;
		}
	}

	.toaster {
		top: @main-header-height;
	}

	.uimodalbox,
	#uimodalbg,
	.uimodalbg,
	.uimodalbox .uimodalcontainer {
		top: @main-header-height;
		left: @main-nav-width;
		height: ~"calc(100% - @{main-header-height})";
		width: ~"calc(100% - @{main-nav-width})";

		@media screen and (min-width: 1601px) {
			width: ~"calc(100% - @{main-nav-width-collapse})";
			left: @main-nav-width-collapse !important;
		}

		@media screen and (max-width: 991px) {
			width: 100%;
			left: 0;
		}

		@media screen and (max-width: 767px) {
			top: 52px;
			height: calc(100% - 52px);
		}
	}

	@media screen and (min-width: 992px) and (max-width: 1600px) {
		&.menu-show-full {
			#uimodalbg,
			.uimodalbg,
			.uimodalbox,
			.uimodalbox .uimodalcontainer {
				left: @main-nav-width-collapse;
				width: ~"calc(100% - @{main-nav-width-collapse})";
			}
		}
	}

	// Schedules
	.schedules {
		margin-bottom: 24px;
		.sch-nav {
			background-color: transparent;
			border: 0;
		}

		.sch-heading-text {
			font-family: Arial, sans-serif, Helvetica Neue;
			font-weight: 400;
			line-height: 1.25em;
			color: #344356;

			@media screen and (min-width: 1601px) {
				font-size: 26px;
			}

			@media screen and (max-width: 1600px) {
				font-size: 20px;
			}

			@media screen and (max-width: 767px) {
				font-size: 18px;
			}
		}

		ul.sch-nav-options {
			li.nav-box {
				margin: 0;
				padding: 8px 20px;
				min-width: 125px;
				line-height: 24px;
				box-shadow: 0 5px 20px #00378033;
				border-bottom: 0;

				&:first-child {
					border-top-left-radius: 30px;
					border-bottom-left-radius: 30px;
				}

				&:last-child {
					border-top-right-radius: 30px;
					border-bottom-right-radius: 30px;
				}
			}
		}
	}
}
